'use client';

import { useChat } from '@/context/ChatContext';
import { useState } from 'react';
import MessageBubble from './MessageBubble';

export default function ChatWindow() {
  const { userName, messages, sendMessage } = useChat();
  const [input, setInput] = useState('');

  const handleSend = () => {
    if (input.trim()) {
      sendMessage(input.trim());
      setInput('');
    }
  };

  return (
    <div style={{
      height: '100vh',
      background: '#111827',
      color: 'white',
      display: 'flex',
      flexDirection: 'column',
      padding: '1rem',
    }}>
      <h1 style={{ textAlign: 'center', fontSize: '1.5rem' }}>ðŸ’¬ Bienvenido {userName}</h1>

      <div style={{
        flex: 1,
        overflowY: 'auto',
        padding: '1rem',
        marginTop: '1rem',
        background: '#1f2937',
        borderRadius: '12px',
      }}>
        {messages.map((msg, i) => (
          <MessageBubble
            key={i}
            sender={msg.sender}
            text={msg.text}
            isMine={msg.sender === userName}
          />
        ))}
      </div>

      <div style={{ display: 'flex', marginTop: '1rem' }}>
        <input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={(e) => e.key === 'Enter' && handleSend()}
          style={{
            flex: 1,
            padding: '0.75rem',
            borderRadius: '12px 0 0 12px',
            border: 'none',
            outline: 'none',
          }}
          placeholder="Escribe algo..."
        />
        <button
          onClick={handleSend}
          style={{
            background: '#10b981',
            color: 'white',
            padding: '0 1.5rem',
            borderRadius: '0 12px 12px 0',
            border: 'none',
          }}
        >
          Enviar
        </button>
      </div>
    </div>
  );
}
