export default function MessageBubble({
    sender,
    text,
    isMine,
  }: {
    sender: string;
    text: string;
    isMine: boolean;
  }) {
    return (
      <div style={{
        textAlign: isMine ? 'right' : 'left',
        marginBottom: '8px',
      }}>
        <div
          style={{
            display: 'inline-block',
            background: isMine ? '#4ade80' : '#e5e7eb',
            color: isMine ? 'white' : 'black',
            padding: '10px 15px',
            borderRadius: '20px',
            maxWidth: '60%',
          }}
        >
          <div style={{ fontSize: '0.8rem', opacity: 0.8 }}>
            {isMine ? 'TÃº' : sender}
          </div>
          <div>{text}</div>
        </div>
      </div>
    );
  }
  